
* {box-sizing: border-box}
body {font-family: Verdana, sans-serif; margin:0;overflow-x: hidden;overflow-y: auto;}
.mySlides {display: none}
.topnav {
  overflow: hidden;
  position: fixed;
  width:100vw;
  bottom: 0px;
}

.topnav a {
 
margin-left:25px;
font-size: 12px;
margin-top: -6px;
 
}
@media only screen and (max-width: 600px) {
  .topnav a {
    margin-left:-5px;

  }
}

.topnav a:hover {
  opacity:.9!important
}
.active{
  opacity:1!important;
}

.slideshow-container {
  max-width: 100vw;
  position: absolute;
  margin: auto;
  height:100vh;
}
.prev, .next {
  cursor: pointer;
  position: fixed;
  top: 45.2%;
  width: auto;
  
  color: white;
  transition: 0.6s ease;
  border-radius: 0;
  user-select: none;
  border:none;
  z-index:2;
}
.next {
  right: 0;
  border-radius:0;
}
.prev{
  left:0;
}
.fade {
  -webkit-animation-name: fade;
  -webkit-animation-duration: 1.5s;
  animation-name: fade;
  animation-duration: 1.5s;
}

@-webkit-keyframes fade {
  from {opacity: .4} 
  to {opacity: 1}
}

@keyframes fade {
  from {opacity: .4} 
  to {opacity: 1}
}
.prev:hover ,  .next:hover {
  color:#0A1623;
  text-decoration: none;
   opacity:1;
}
.next:hover ~ .txt {
  background-color: #404951;
  color:white;
  text-decoration: none;
  opacity:1;
} 

.txt{
  cursor: pointer;
  position:fixed;
  top: 45%;
  width: 150px;
  padding: 10.2px;
  padding-left: 45px;
  color: white;
  font-weight:lighter;
  font-size: 15px;
  transition: 0.6s ease;
  border-radius: 0 0 0 0;
  user-select: none;
  background-color: black;
  z-index: 0;
  right:0;
  opacity:0;
  border:none;
  border-radius: 5px;
  }

.demo:hover .dem{
  opacity:1!important;
  display:block;
}

.txt2{
  cursor: pointer;
  position:fixed;
  top: 45%;
  width: 150px;
  padding: 10.2px;
  padding-left: 55px;
  color: white;
  font-weight:lighter;
  font-size: 15px;
  transition: 0.6s ease;
  border-radius: 0 0 0 0;
  user-select: none;
  background-color: black;
  z-index: 0;
  opacity:0;
  border:none;
  border-radius: 5px;

  }

 .prev:hover ~ .txt2{
  background-color:#404951;
  color:white;
  text-decoration: none;
  opacity:1;
}
nav{
  background-color: #232E39!important;
   height: 55px!important;
}
.demo{
  color: white!important;
  opacity: .4;
  text-decoration: none!important
}
.dem{
  height: 6px;
  width: 6px;
  border-radius: 100%;
  background-color: #F1A002;
  opacity:.4
}
